@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
	font-family: 'AC7-Regular';
	src: local('AC7-Regular'), url(./fonts/ACES07_Regular.otf) format('opentype');
	/* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: 'AC7-Regular', sans-serif;

	color: rgb(168, 255, 255);
	text-shadow: 0 0 5px rgb(168, 255, 255), 0 0 15px #228dff, 0 0 35px #228dff;
	background: url('./images/ac7-bg-img.jpg') no-repeat center center fixed;
	background-color: rgb(12,35,87);
	
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;

	-webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

#modal-begin {
	background-color: rgb(35, 35, 41);
}

.begin-button {
	text-align: center;
	-webkit-align-self:center;-moz-align-self:center;align-self:center;
}

.no-text-glow {
	text-shadow: none;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}


.breadcumb-logo {
	width: 60px;
	height: 60px;
	border: solid #fff 1px;
	box-shadow: 0 0 5px rgb(168, 255, 255), 0 0 15px #228dff, 0 0 35px #228dff;
}