.radar-container {
	width: 400;
	max-width: 400px;
	min-height: 400px;
}

.bar-container {
	min-width: 350px;
	min-height: 400px;
}

.active-caret {
	width: 10px;
	height: 24px;
	background: rgb(126,253,45);
	background: linear-gradient(90deg, rgb(25, 233, 181) 10%, rgba(255,255,255,1) 100%);
	box-shadow: 0 0 5px rgb(255, 168, 255), 0 0 15px rgb(155, 255, 94), 0 0 35px rgb(111, 238, 33);
}

.inactive-caret {
	width: 10px;
	height: 24px;
	visibility: hidden;
}

.active-player-list-item {
	color: rgb(180, 199, 231);
}

.inactive-player-list-item {
	color: rgb(90, 90, 90);
	text-shadow: none;
}

.player-list-item-name {
	cursor: pointer;
}

.inactive-player-list-item:hover {
	color: rgb(119, 137, 172);
}

.pilot-picture {
	width: auto;
	max-height: 255px;
}


#modal-information {
	background-color: rgba(35, 35, 41, 0.75);
	display:none;
	height: 100%;
} 